      link: 'https://www.meetup.com/Bitcoin-Munich/events/jbbfbqybchbjb/',
      description: '<p>(English description below.)</p> <p>‚Äî</p> <p>Willkommen beim ersten und originalen Stammtisch der M√ºnchner Bitcoin-Enthusiasten!</p> <p>####</p> <p>Aus nachvollziehbaren Gr√ºnden treffen wir uns diesmal nur virtuell!</p> <p>Die Idee ist, das informelle, ungezwungene Stammtischformat beizubehalten. Es gibt kein offizielles Programm, schaltet euch einfach rein und plaudert mit! Wie in der physischen Version zielen wir auf eine gute Mischung aus neugierigen Neulingen und Veteranen ab. Zwecks dem lokalen Flair sollte ein Bezug zu M√ºnchen bei euch vorhanden sein.</p> <p>Software: Jitsi Meet (free/libre &amp; open source, self-hosted, kein Logging, keine Registrierung n√∂tig), funktioniert einfach √ºber Browser oder App.</p> <p>Genaue Links und Info siehe unten (Kommentarsektion)!</p> <p>####</p> <p>Wir treffen uns zur zwanglosen Plauderei √ºber die technischen sowie √∂konomischen und gesellschaftlichen Aspekte von Bitcoin und anderen Kryptow√§hrungen. Auch werden wir wohl diskutieren, worum es bei dem ganzen Aufruhr um Blockchains eigentlich geht.</p> <p>Egal ob Neulinge oder Profis, ob Urbayer oder Weltb√ºrger, alle sind herzlich willkommen! Ihr k√∂nnt immer reichlich interessante Leute treffen, und ihr k√∂nnt euch gut f√ºr aufregende Projekte zusammenfinden!</p> <p>[...]</p> <p>Dieses informelle Stammtisch-Format findet jeden ersten Mittwoch eines Monats statt und hat keine offiziellen Vortr√§ge oder Pr√§sentationen. Wir setzen weitere Veranstaltungen an zu diesem Zweck an unterschiedlichen Orten. Bitte werft einen Blick auf unsere √úbersichtsseite, um diese zu finden.<p>Welcome to the first and original Stammtisch of the Munich Bitcoin enthusiasts!</p> <p>####</p> <p>Due to obvious reasons we\'ll only meet virtually this time.</p> <p>The idea is to keep the informal and easy format of the Stammtisch. There is no official program, just join in and chat with us! Like in the physical version, we\'re aiming for a good mixture of curious newcomers and veterans. For the local flair you should have a reference to Munich.</p> <p>Software: Jitsi Meet (free/libre &amp; open source, self-hosted, no logging, no registration necessary), works simply via browser or app.</p> <p>Exact links and info see below (comment section)!</p> <p>####</p> <p>We meet to casually chat about the technical as well as the economic and societal aspects of Bitcoin and other crypto-currencies. Also we might debate what the fuzz around blockchains is all about.</p> <p>No matter if newbies or pros, if primordial Bavarian or world citizen, everyone is sincerely welcome! You can always meet many interesting people, and you can come together for exciting projects!</p> <p>[...]</p> <p>This informal Stammtisch format takes place every first Wednesday of a month and has no official talks or presentations. We schedule other events for that purpose in various different locations. Please take a closer look at our overview page to find those. ',
