      link: 'https://www.meetup.com/Projektmanagement-Meetup-Roundtable/events/269466520/',
      description: '<p>MS-Teams Meeting (see Online Link here)</p> <p><p>Agenda:</p> <p>18:00 Uhr ‚Äì Begr√º√üung, Einleitung, Infos zum Ablauf</p> <p>18:15 Uhr ‚Äì Vortrag 1: -&gt; ‚ÄûOffice 365 ‚Äì Implementierung f√ºr das Arbeiten im Homeoffice: Weitere nachhaltige O365 Erweiterungen f√ºr die Bereiche Ideenmanagement, Business Cases und Projektkollaboration mit der Edison 365 IT-Suite</p> <p>Vortrag Herr Andreas Hock, Partner 3Pworx GmbH</p> <p>Aufgrund der derzeitigen Corona Krise sind gut ausgebaute und werthaltige Cloud- und Homeoffice L√∂sungen in aller Munde. Das Arbeiten von zu Hause aus soll vor allem Spa√ü machen und auch aus der Sicht der Produktivit√§t ein hohes Zielma√ü erreichen.</p> <p>Eine der f√ºhrenden L√∂sungen, um effizient und professionell vom Homeoffice aus arbeitsf√§hig zu sein ist Office 365.</p> <p>Office 365 bietet vor allem interessante L√∂sungen und Apps aus dem Bereich der digitalen Zusammenarbeit, wie MS Teams, SharePoint, usw. Agile Methoden werden ebenfalls reichlich unterst√ºtzt.</p> <p>Dieses vorhandene Toolpaket kann durch L√∂sungen aus dem Bereich Ideenmanagement, Business Cases und Projekt-Kollaboration mit den fertigen Edison 365 Produktl√∂sungen intelligent und nachhaltig erweitert werden.</p> <p>Die nahtlose Integration ins Office 365 Umfeld ist sehr einfach darstellbar. Weitere Schnittstellen zu MS Teams, usw. sind schl√ºsselfertig.</p> <p>Einen ersten √úberblick der Produkte finden Sie hier: <p>19:15 Uhr ‚Äì Vortrag 2: ‚ÄûOrthogonales Projektmanagement ‚Äì Nicht planen, nicht vereinbaren, sondern entscheiden lassen</p> <p>Vortrag von Herrn Daniel Juling</p> <p>Weder die Modelle des klassischen PM, noch die Modelle des agilen PM sind ausreichend geeignet, um die Entscheidungsintelligenz der Evolution zu nutzen. Mit dem orthogonalen Denken er√∂ffnen Sie sich eine Methode, die immense Skalierungseffekte zul√§sst, indem Sie bewusst Entscheidungen an den Markt delegieren und in Ihre Projekte einbinden.</p> <p>Lernen Sie das orthogonale PM kennen und erweitern Sie Ihre Methoden-Kiste um ein machtvolles Werkzeug.</p> <p>20:15 Uhr ‚Äì Vortrag 3: ‚ÄûProjektmanagement Trainings vor dem Wandel der Zeit. Agil ‚Äì Klassisch ‚Äì Hybrid | Pr√§senztrainings ‚Äì Online Trainings</p> <p>Vortrag von Herrn Andreas Hock, Partner 3Pworx GmbH und Herrn Helmut Schneider, CPS Consulting</p> <p>Die ganze Welt steht derzeit vor nie da gewesenen Ver√§nderungen. Was gestern noch g√ºltig war hat morgen vielleicht schon keine Bedeutung mehr.</p> <p>In den gegenw√§rtigen Zeiten unvorhersehbarer Ereignisse ist es f√ºr Firmen wichtiger denn je, sich diesen Herausforderungen flexibel zu stellen um weiterhin handlungsf√§hig zu bleiben.</p> <p>Ein Umdenken ist vor allem im Bereich der Zusammenarbeit n√∂tig. Agilit√§t und Flexibilit√§t, Virtualisierung und Digitalisierung sind jetzt nicht nur viele Firmen sondern auch f√ºr den Bereich Aus- und Weiterbildung existenziell. Wir haben daher unsere Projektmanagement Trainings auf online Trainings erweitert um Ihnen die M√∂glichkeit zu bieten auch in Zeiten von (verordnetem) Home Office an Aus- und Weiterbildungen teilzunehmen. Wie auch in den klassischen Pr√§senztrainings liegt in unseren online Trainings neben der Wissensvermittlung ein starker Fokus auf Zusammenarbeit und Interaktion zwischen den Teilnehmern um somit den bestm√∂glichen Mehrwert der Trainings f√ºr Sie zu generieren.</p> <p>Die Nachfrage nach Trainings in agilen Methoden ist daher hoch. Wir stellen Ihnen mit unserem Kooperationspartner CPS Consulting &amp; Projektmanagement unser 3Pworx Trainingsprogramm im Bereich klassisches/hybrides und agilen Projektmanagement genauer vor.</p> <p>21:30 Uhr: Q&amp;A</p> <p>Ca. 22:00 Uhr ‚Äì Ende des 3Pworx 2. Agilen Trendforums Mai 2020 digital</p> ',
